import "core-js/modules/es.parse-int.js";
import { h, ref, watch, onMounted } from "vue";
import { useStore } from "vuex";
import { useRouter } from "vue-router";
import { ElMessage, ElNotification } from "element-plus";
import 'element-plus/es/components/message/style/index'
import 'element-plus/es/components/notification/style/index'

import { UserFilled, Menu, RefreshRight } from "@element-plus/icons-vue";
import BaseMenu from "../components/BaseMenu.vue";
import FilterForm from "../components/FilterForm.vue";
import DashboardTable from "../components/DashboardTable.vue";
import axios from "../../../shared/axios.js";
export default {
  components: {
    BaseMenu: BaseMenu,
    FilterForm: FilterForm,
    DashboardTable: DashboardTable
  },
  setup: function setup() {
    var store = useStore();
    var router = useRouter();
    var filter = ref(false);
    var filterCount = ref(1);
    var totalRows = ref(1);
    var currentPage = ref(1);
    var pageSize = ref(50);
    var drawer = ref(false);

    var toggleDrawer = function toggleDrawer() {
      drawer.value = !drawer.value;
    };

    var getPages = function getPages() {
      axios.post("/dashboard/table/count", store.getters.filters).then(function (response) {
        totalRows.value = parseInt(response.data.count, 10);
      }).catch(function () {
        ElMessage.error("Упс! Не удалось загрузить страницы!");
      });
    };

    var update = ref(false);

    var toggleUpdate = function toggleUpdate() {
      getPages();
      update.value = !update.value;
    };

    var handleUpdate = function handleUpdate() {
      getPages();
      ElNotification({
        title: "Новая заявка",
        message: h("i", {
          style: "color: teal"
        }, "Появилась новая заявка!")
      });
    };

    watch([currentPage, pageSize], function () {
      store.dispatch("setOptions", {
        limit: pageSize.value,
        page: currentPage.value
      });
    });
    watch(filter, function () {
      if (!filter.value) {
        for (var i = 1; i <= filterCount.value; i++) {
          store.dispatch("removeFilters", i);
        }
      }

      filterCount.value = 1;
    });

    var logout = function logout() {
      axios.post("/dashboard/auth/logout").then(function () {
        router.push("/dashboard/login");
      });
    };

    onMounted(function () {
      getPages();
    });

    var handleFilter = function handleFilter(filterParam) {
      if (filterParam.delete) {
        store.dispatch("removeFilters", {
          id: filterParam.id
        });
        filterCount.value = filterParam.id;
      } else {
        store.dispatch("addFilters", filterParam);
        filterCount.value = filterCount.value === 8 ? 8 : filterParam.id + 1;
      }

      getPages();
    };

    var handleError = function handleError() {
      ElMessage.error("Упс! Что-то пошло не так");
    };

    return {
      filter: filter,
      filterCount: filterCount,
      drawer: drawer,
      toggleDrawer: toggleDrawer,
      handleUpdate: handleUpdate,
      update: update,
      toggleUpdate: toggleUpdate,
      totalRows: totalRows,
      currentPage: currentPage,
      pageSize: pageSize,
      logout: logout,
      handleFilter: handleFilter,
      handleError: handleError,
      UserFilled: UserFilled,
      Menu: Menu,
      RefreshRight: RefreshRight
    };
  }
};